<template>
  <div>
  
        
        <div class="demo">
      <app-Header></app-Header>
      
        <div class="content">
        <app-chart></app-chart>
        </div>
       </div>
        <div class="container audi">
        <app-Audience></app-Audience>
        </div>
        <div class="foot" style="margin:60px;">
        <app-footer></app-footer>
        </div>
  </div>
  </div>
</template>

<script>
import Header from './Header';
import Chart from './Chart.vue';
import Footer from './Footer.vue';
import Audience from './Audience.vue';
export default {
 components:{
        appAudience: Audience,
        appHeader: Header,
        appChart: Chart,
        appFooter: Footer
    }
   
}
</script>

<style scoped>
.demo{
  background: linear-gradient(90deg, #FD5A49 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
 height:620px;
 margin:0px;
  }
.content{
  margin-top:50px;
}
.audi{
  margin-left:auto;
  margin-right:  auto;
  display:block;
  width: 100%;
  background: linear-gradient(#e3e3e3,red,white);
  height:500px;
  


  
}

</style>



