<template>
<div>
<div class="sur">
<app-header></app-header>
<div class="box">
  <div class="inner">
    <span>Survey Poll</span>
  </div>
  <div class="inner">
    <span>Survey Poll</span>
  </div>
</div>
  <div class="center" style="margin-top: 100px;">
      <div class="col-md-4">
        <loader v-if="showLoader"></loader>
        <div class="boox">
  <img src="static/image/surveypoll.jpeg" style="width=180px; height:110px; border-radius:50%;display:flex;
    display: block;
    margin-left: auto;
    margin-right: auto; margin-top:-60px;align:center;">
  
        <div class="col-12">
        <label> User Name</label>
            <input type="text" v-model="name" class="form-control reset-border" placeholder="Enter Name">
        </div>
        <div class="col-12" style="padding-top: 10px;">
        <label>Poll code</label>
            <input type="text" v-model="code" class="form-control reset-border" placeholder="Enter Code (Q1005)">
        </div>
        <div class="col-12">
            <div style="text-align: center;color: red;" v-if="isIncorrectCode">Incorrect Code</div>
        </div>
        <div class="col-12">
            <button @click="validateCode" style="margin-top: 10px;" class="btn btn-primary btn-block reset-border" :disabled="disable">SUBMIT</button>
        </div>
      </div>
  </div>
</div>
</div>
</div>
</template>

<script>
import axios from 'axios';
import Loader from './Loader';
import Header from './Header';
export default {
    components:{
        Loader,
            appHeader:Header
    },
    data(){
        return{
            name: '',
            code: '',
            isIncorrectCode: false,
            showLoader: false,
            quiz: ''
        }
    },
    methods:{
        validateCode: function(){
            
            socket.emit('addUser', this.name);
            this.showLoader = true; 
            axios.get('./static/json/questions.json')
            .then((response) => {
                //console.log(response.data);

                for(let data of response.data){
                    if(data.code == this.code){
                        this.quiz = data.quiz;

                        this.$router.push({name: 'Quiz1', params:{quizData: this.quiz}})
                        break;
                    }
                }

                if(this.quiz == ''){
                    this.isIncorrectCode = true;
                }

                this.showLoader = false;
                console.log(this.quiz);

            })
            .catch((error) => {
                console.log(error);
                this.showLoader = false;
            });

        }
    },
    computed:{
        'disable': function(){
            if(this.code.length < 4 || this.name.length < 4){
                return true;
            }

            return false;
        }
    }

}
</script>


<style scoped>
    .sur{  
    background: linear-gradient(45deg,#FD5A49,#FDDC98);
    height: 730px  ;
     
    }
    .center{
        display: flex;
        justify-content: center;
    }

    .reset-border{
        border-radius: 0;
    }
    
 .boox{
        /* width: 90%; */
        margin-top: 0px;
        min-height: 250px;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 15px 20px 35px;
        -webkit-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        -moz-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        background: white;
    }
    
    html,
body {
	height: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	//background-color: navajowhite;
}

.box { 
    margin-top:100px;
  //  margin-left:300px;
    min-margin-left:150px;
	display: flex;
        justify-content: center;
}

.box .inner {
	width: 420px;
	height: 110px;
	line-height: 100px;
	font-size: 3em;
	font-family: sans-serif;
	font-weight: bold;
	white-space: nowrap;
	overflow: hidden;
}

.box .inner:first-child {
	background-color: indianred;
	color: darkred;
	transform-origin: right;
	transform: perspective(100px) rotateY(-10deg);
}

.box .inner:last-child {
	background-color: lightcoral;
	color: antiquewhite;
	transform-origin: left;
	transform: perspective(100px) rotateY(10deg);
}

.box .inner span {
	position: absolute;
	animation: marquee 5s linear infinite;
}

.box .inner:first-child span {
	animation-delay: 2.5s;
	left: -100%;
}

@keyframes marquee {
	from {
		left: 100%;
	}

	to {
		left: -100%;
	}
}
</style>


