<template>
<div>


    <div id="adminis" style="background-image: linear-gradient(to right top, #c8d523, #a5de4a, #80e46d, #57e990, #12ebb2); height: 730px;">
    <app-header></app-header>
    
  <div class="center" style="margin-top: 100px;">
    <div class="boox">
  <img src="static/image/admin.jpg" style="width=100%; height:110px; display:block; margin-left: auto; margin-right: auto; border-radius:50%; margin-top:-60px; margin-bottom:20px;">
  
      <div class="col-md-10 col-md-12">
        <loader v-if="showLoader"></loader>
        
        <div class="col-12" style="padding-top: 10px;">
            <input type="text" v-model="code" class="form-control reset-border" placeholder="Enter Code">
        </div>
        <div class="col-12">
            <div style="text-align: center;color: red;" v-if="isIncorrectCode">Incorrect Code</div>
        </div>
        <div class="col-12">
            <button @click="validateCode" style="margin-top: 10px;" class="btn btn-primary btn-block reset-border" :disabled="disable">START</button>
        </div>
      </div>
  </div>
</div>
</div>
</div>
</div>

</template>

<script>
import axios from 'axios';
import Loader from './Loader';
import Header from './Header';


export default {
    components:{
        Loader,     
       appHeader:Header,
    
    },
    data(){
        return{
            code: '',
            isIncorrectCode: false,
            showLoader: false,
            quiz: ''
        }
    },methods:{
        validateCode: function(){
            //console.log(this.code);

            this.showLoader = true;
            axios.get('/static/json/questions.json')
            .then((response) => {
                //console.log(response.data);

                for(let data of response.data){
                    if(data.code == this.code){
                        this.quiz = data.quiz;

                        this.$router.push({name: 'Poll', params:{quizData: this.quiz}})
                        break;
                    }
                }

                if(this.quiz == ''){
                    this.isIncorrectCode = true;
                }

                this.showLoader = false;
                console.log(this.quiz);

            })
            .catch((error) => {
                console.log(error);
                this.showLoader = false;
            });

        }
    },
    computed:{
        'disable': function(){
            if(this.code.length < 4){
                return true;
            }

            return false;
        }
    }

}
</script>

<style scoped>

    .center{
        display: flex;
        justify-content: center;
    }

    .reset-border{
        border-radius: 0;
    }
    
 .boox{
        /* width: 90%; */
        margin-top: 100px;
        min-height: 250px;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 15px 20px 35px;
        -webkit-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        -moz-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        background: white;
    }
    .center{
        display: flex;
        justify-content: center;
    }
</style>


