<template>
<div>
  <div class="rule">
    <app-header></app-header>
  <div class="container content">
  <h2 style="margin-top:100px;">RULES</h2>
  <div class="alert alert-success">
    <p> We will provided 15 second or 20 second to the user to answer the question.</p>
  </div>
  <div class="alert alert-info">
    <p> User has to give response within given time period.</p>
  </div>
  <div class="alert alert-warning">
    <p>User have to enter the Poll code</p>
  </div>
</div>
</div>
</div>
</div>
</template>

<script>
import Header from './Header';
export default {
components:{
       appHeader:Header,
}
}
</script>

<style scoped>
        p{
            font-size:20px;
        }
        h2{
            font-family: algerian;
            text-align: center;
        }
        .rule{
            background: linear-gradient(45deg,#FFE9D0,#FD7153);
            height: 728px;
          
        }

</style>



