<template>
<div>
<div class="color" style="background:linear-gradient(45deg,#FFE9D0,#FD7153);height: 910px;">
    <app-Header></app-Header>
    <header></header>
    <div class="content">
<div class="container">
   <h1> ABOUT US</h1> 
        <div class="container">
        <div class="alert alert-success">
           <p> We are developing an live polling application by taking advantage of the centralized database with a web interface. The main concept of this project is to build a website, which should be able to allow people to give their question live. </p>
            </div>
            <div class="alert alert-info">
            <p>Get real time valuable feedback in which we can get interests in various sessions or speakers.</p>
            </div>
            <div class="alert alert-warning">
<p>Combine single  as well as  multiple choice question and integrate live visualization of incoming answers. Response appear in an animated graphs and charts.</p>

            </div>
        </div>
</div>
</div>
</div>
</div>
</template>

<script>
import Header from './Header';



export default {
     components:{
    
        appHeader:Header,
        
     }

}

</script>

<style scoped>
        h1{
            font-family: algerian;
            text-align: center;
            margin: 100px;
            margin-bottom:20px;
            font-size:30px;
        }
        p{
            font-size:20px;
        }
        .color{
            background:  linear-gradient(45deg,#FFE9D0,#FD7153);
            height: 800px;
            }
</style>



